navbar{
  position: fixed;
  top: 0vh;
  z-index: 1;
  width: 100vw;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  background-color: rgba(0, 0, 0, 0.6);
  -webkit-box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.6);
          box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.6);
}
.hideNavbar{
  -webkit-animation-name: to-top;
          animation-name: to-top;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
    top: -10vh;

}

@-webkit-keyframes to-top {
  from {
    top:0vh;
  }

  to {
    top: -10vh;
  }
}

@keyframes to-top {
  from {
    top:0vh;
  }

  to {
    top: -10vh;
  }
}

.showNavbar{
  visibility: visible;
  -webkit-animation-name: from-top;
          animation-name: from-top;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
}

@-webkit-keyframes from-top {
  from {
    top:-10vh;
  }

  to {
    top: 0vh;
  }
}

@keyframes from-top {
  from {
    top:-10vh;
  }

  to {
    top: 0vh;
  }
}

navbar a{
  background-color: transparent;
  border: none;
  font-weight: bold;
  color: white;
  margin: 10px 18px 10px 18px !important;
  text-decoration: none;
}

navbar a:hover {
  border-bottom: 5px solid steelblue !important;
}

.menu{
  position: fixed;
  top: 5vh;
  left: 5vw;
  color: white;
  font-size: 20px;
  width: 35px;
  height: 35px;
  z-index: 2;
  border-radius: 50%;
  border: none;
  background-color: rgba(0, 0, 0, 0.6);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-pack: center;
      justify-content: center;
}


.menu:hover {
  color: steelblue;
  border: 4px solid steelblue;

}

.menu:active{
  -webkit-animation-name: rotate;
          animation-name: rotate;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
}

@-webkit-keyframes rotate {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg)
  }

  to {
    -webkit-transform: rotate(3600deg);
            transform: rotate(3600deg)
  }
}

@keyframes rotate {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg)
  }

  to {
    -webkit-transform: rotate(3600deg);
            transform: rotate(3600deg)
  }
}

@media (max-width: 600px){
  navbar{
    -ms-flex-direction: column;
        flex-direction: column;
    position: fixed;
    left: 5vw;
    top: 20vh;
    height: 60vh;
    width: 90vw;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -webkit-animation-name: from-left;
            animation-name: from-left;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    color: black !important;
    border: 4px solid black;
    border-radius: 5px;
    background-color: black;
  }
@-webkit-keyframes from-left {
  from {
    left:-100vw;
  }

  to {
    left: 0vw;
  }
}
@keyframes from-left {
  from {
    left:-100vw;
  }

  to {
    left: 0vw;
  }
}
}




