
Header{
  height: 100vh;
  position: relative;
  background: white;
  color: rgb(92, 136, 172)
}

.contain{
  position: absolute;
  left: 0;
  top: 0;
  height: 66vh;
  width: 66vw;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;

}

.header-title{
  font-size: 4vmin;
  z-index: 1;
}

.profile{
  z-index: 1;
  width: 150px;
  height: 150px;
  max-width: 25vmin;
  max-height: 25vmin;
  border-radius: 50%;
  border: 1vh solid rgb(92, 136, 172);
}
.poligone{
  position: absolute;
  width: 100vw;
  height: 100vh;
}

header .poligone{
    left: 0vh;
  top: 0vh;
}

#first{
-webkit-clip-path: polygon(  66% 100%, 77% 89%, 77% 0%, 55% 0%, 55% 89%);
        clip-path: polygon(  66% 100%, 77% 89%, 77% 0%, 55% 0%, 55% 89%);
overflow: hidden;
}
#first img{
  height: 100vh;
  width: 100vw;
  position: relative;
}


